@import url(https://fonts.googleapis.com/css?family=Libre+Barcode+128+Text&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bungee+Inline&display=swap);
@import url(https://fonts.googleapis.com/css?family=Faster+One&display=swap);
body {
    margin: 0;
    border: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background-image: -webkit-linear-gradient(290deg, white, white,white, #5DADE2);
    background-image: linear-gradient(160deg, white, white,white, #5DADE2);
    scroll-behavior: smooth;
}


.aboutMeBox {
    background: rgba(255, 255, 255, 0.5);
    padding: 10px 20px 10px 20px;
    border-left: 5px solid black;
    border-right: 5px solid black;
}

.aboutMeStyle {
    font-family: 'Faster One', cursive;
    font-size: "1000px";
}

.logo {
    font-family: 'Pacifico', cursive;
    
}

.link:hover {
    border-left: 5px solid rgb(0, 183, 255)
}

.contactUsFormat {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.formFormat {
    margin: 5px 0 5px 0;
}

.parallaxStyle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    height: 100vh;
    background-image: url(/website/static/media/wall.0babc688.jpg);
    background-size: cover;
    background-attachment: fixed;
}

.sectionHeading {
    margin-top: 20px;
    text-align: center;
    font-size: 50px;
    color: black;
    border-bottom: 5px solid black;
    border-right: 10px solid black;
    font-family: 'Faster One', cursive;
}

.creatorName {
    font-family: 'Libre Barcode 128 Text', cursive;
    color: black;
    font-size: 50px;
    text-align: center;
}

@-webkit-keyframes pulse {
    25% {
      -webkit-transform: scale(1.2);
      transform: scale(1.2);
    }
    75% {
      -webkit-transform: scale(0.8);
      transform: scale(0.8);
    }
  }
  
@keyframes pulse {
25% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
}
75% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
}
}

.pulse {
display: inline-block;
-webkit-transform: translateZ(0);
transform: translateZ(0);
box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

.pulse:hover {
-webkit-animation-name: pulse;
animation-name: pulse;
-webkit-animation-duration: 1s;
animation-duration: 1s;
-webkit-animation-timing-function: linear;
animation-timing-function: linear;
-webkit-animation-iteration-count: infinite;
animation-iteration-count: infinite;
}

.hide{
    visibility: hidden;
}



